<template>
  <div>
    <Header :title="title"></Header>
    <img :src="img.user" alt="" class="top_user">
    <!--轮播图-->
    <div class=" swiper-container foods_kind">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <router-link v-for="kind1 in kindOne"
                       :to="{name:'Shopshow',query:{title:kind1.title,restaurant_category_id:kind1.id,geohash:$store.state.city.geohash}}"
                       :key="kind1.id"
                       class="link_to_food">
            <img :src="'https://fuss10.elemecdn.com'+kind1.image_url" alt="">
            <span>{{kind1.title}}</span>
          </router-link>
        </div>
        <div class="swiper-slide">
          <router-link class="link_to_food" v-for="kind2 in kindTwo"
                       :to="{name:'Shopshow',query:{title:kind2.title,restaurant_category_id:kind2.id,geohash:$store.state.city.geohash}}"
                       :key="kind2.id">
            <img :src="'https://fuss10.elemecdn.com'+kind2.image_url" alt="">
            <span>{{kind2.title}}</span>
          </router-link>
        </div>
      </div>
      <!--分页器-->
      <div class="swiper-pagination"></div>
    </div>
    <!--附近商家-->
    <div class="business">
      <img :src="img.business" alt="" style="width: .6rem;vertical-align: middle">
      <span>附近商家</span>
    </div>
    <goodlist :Infor="shopInfor"></goodlist>
    <!--导航栏-->
    <footernav class="footernav"></footernav>
  </div>
</template>

<script>

  import search from "./img/搜索.png";
  import user from "./img/商户线性.png";
  import business from "./img/商家 (3).png";

  import Swiper from "../../../static/js/swiper-4.2.2.min";
  import Swipercss from "../../../static/css/swiper-4.2.2.min.css";

  import footernav from "../../components/footernav/footernav";
  import goodlist from "../../components/goodslist/goodslist";
  import header from "./Nav"

  export default {
    name: "homepage",
    data() {
      return {
        title: "",
        img: {business, search, user},
        foodkinds: [],
        kindOne: [],
        kindTwo: [],
        shopInfor: []
      }
    },
    created() {
      if (!sessionStorage.getItem("local")) {
        this.title = "未知位置请重新定位"
      } else {
        this.title=sessionStorage.getItem("local")
      }
      //swiper
      this.$http.get("http://cangdu.org:8001/v2/index_entry").then((response) => {
        this.foodkinds = this.publicfunction.sliceArray(response.data, 8)
        this.kindOne = this.foodkinds[0];
        this.kindTwo = this.foodkinds[1];
        setTimeout(function () {
          var mySwiper = new Swiper('.swiper-container', {
            loop: true,
            // 如果需要分页器
            pagination: {
              el: '.swiper-pagination'
            }
          })
        }, 200)
      })
      //附近商家
      this.$http.get("http://cangdu.org:8001/shopping/restaurants?latitude=" + this.$store.state.city.latitude + "&longitude=" + this.$store.state.city.longitude).then((response) => {
        console.log(response.data)
        this.shopInfor = response.data
      })
    },
    components: {
      footernav,
      goodlist,
      Header: header
    }
  }
</script>
<style>
  .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 0.25rem;
  }
</style>
<style scoped>
  /*top*/
  .top_user {
    width: .9rem;
    height: .9rem;
    position: fixed;
    top: .5rem;
    right: .4rem;
  }

  /*---轮播图----*/
  .swiper-container {
    padding-bottom: .6rem;
    background: white;
  }

  .swiper-slide {
    width: 100%;
  }

  /*单个*/
  .swiper-slide .link_to_food {
    width: 25%;
    display: inline-block;
    text-align: center;
    padding: .3rem 0;
  }

  .swiper-slide .link_to_food img {
    margin-bottom: .6rem;
    width: 1.8rem;
  }

  .swiper-slide .link_to_food span {
    font-size: .55rem;
    color: #666;
    display: block;
    font-weight: 200;
  }

  /*分页器*/
  .swiper-pagination {
    width: 100%;
    background: transparent;
  }

  .footernav {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
  }

  /*附近商家*/
  .business {
    background: white;
    margin-top: .4rem;
    padding-left: .6rem;
    padding-top: 0.4rem;
  }

  .business span {
    color: #999;
    font-size: 0.55rem;
  }
</style>



