<template>
  <div>
    <Header :title="title"></Header>
    <div class="imglogo">
      <img
        src="data:image/jpeg;base64,"
        alt="">
    </div>
    <div class="download">下载饿了么APP</div>
    <div class="btndownload" @click="downloadapp">下载</div>
    <transition enter-active-class="animated bounceIn">
      <div class="login-warn" v-if="show">
        <img :src="warn" alt="">
        <p>IOS用户请前往AppStore下载</p>
        <div class="warn-sure" @click="hides">确认</div>
      </div>
    </transition>
  </div>
</template>

<script>
  import header from "../../components/foodheader/foodheader"
  import warn from "./img/警告.png";

  export default {
    name: "download",
    data() {
      return {
        warn,
        title: "下载",
        show: false
      }
    },
    components: {
      Header: header
    },
    methods: {
      downloadapp() {
        this.show = true
      },
      hides() {
        if (this.show == true) {
          this.show = false;
        }
      }
    }
  }
</script>

<style scoped>
  .imglogo {
    text-align: center;
  }

  .imglogo img {

    border-radius: 1rem;
    margin-top: 1rem;
  }

  .download {
    text-align: center;
    margin-top: .2rem;
    font-size: .8rem;
    color: #666;
    margin-bottom: 1rem;
  }

  .btndownload {
    background-color: #4cd964;
    font-size: .7rem;
    color: #fff;
    text-align: center;
    margin: 0 .7rem;
    line-height: 1.8rem;
    border-radius: .2rem;
    margin-top: .5rem;
  }

  .login-warn {
    width: 12rem;
    background: white;
    border-radius: .25rem;
    position: absolute;
    top: 29.6%;
    left: 2rem;
    text-align: center;
    padding-top: .6rem;
  }

  .login-warn img:nth-of-type(1) {
    width: 3rem;
    height: 3rem;
  }

  .login-warn p {
    font-size: .7rem;
    color: #333;
    margin: .8rem 0;
    font-weight: 200;
    letter-spacing: 0.02rem;
  }

  .warn-sure {
    background-color: #4cd964;
    font-size: .7rem;
    color: #fff;
    padding: .6rem 0;
    border: 1px;
    border-radius: .15rem;
    font-weight: 200;
  }
</style>



